//Sound
const hitSound = new Audio('sounds/hit.mp3')
const ballSound = new Audio('sounds/ping.mp3')
const soundArray = [hitSound, ballSound]


const playSound = (collision) =>
{   
    // const random = soundArray[Math.round(Math.random())]
    const strength = collision.contact.getImpactVelocityAlongNormal()
    if(strength > 3){

        hitSound.volume = 0.5 + (Math.random() * 0.3)
        hitSound.currentTime = 0
        hitSound.play()
    }else if(strength <= 3 && strength > 2){
        hitSound.volume = Math.random()  * 0.2
        hitSound.currentTime = 0
        hitSound.play()
    }

}



