//Utils
const objectsToUpdate = []
const updateMeshes = []
const colors = ['#a69eff', '#c0ff54'];
let randomColor;
const geometry = new THREE.SphereGeometry(1, 24, 24)

const createSphere = (radius, position) => {

    //Object
    randomColor = colors[Math.round(Math.random())]
    const material = new THREE.MeshToonMaterial({
        color: randomColor
    })
    const mesh = new THREE.Mesh(geometry, material)
    mesh.scale.set(radius,radius, radius)
    mesh.castShadow = true
    mesh.position.copy(position)
    scene.add(mesh)

    //Physics Body
    const shape = new CANNON.Sphere(radius)
    const body = new CANNON.Body({
        mass: 1,
        shape: shape,
        material: defaultMaterial
    })
    body.position.copy(position)
    body.addEventListener('collide', playSound)
    world.addBody(body)

    //save objecst to update
    objectsToUpdate.push({
        mesh: mesh,
        body: body
    })
    updateMeshes.push(mesh)

    body.applyLocalForce(new CANNON.Vec3(50,0,0), body.position)

}



//Boxes
const boxGeometry = new THREE.BoxGeometry(1, 1, 1)

const createBoxes = (width, height, depth, position) => {

    //Object
    randomColor = colors[Math.round(Math.random())]
    const material = new THREE.MeshToonMaterial({
        color: randomColor
    })
    const mesh = new THREE.Mesh(boxGeometry, material)
    mesh.position.copy(position)
    mesh.scale.set(width, height, depth)
    mesh.castShadow = true
    scene.add(mesh)

    //Physics
    const shape = new CANNON.Box(new CANNON.Vec3(width/2, height/2, depth/2))
    const body = new CANNON.Body({
        mass: 1,
        shape: shape,
        material: defaultMaterial
    })
    body.position.copy(position)
    body.addEventListener('collide', playSound)
    world.addBody(body)

    objectsToUpdate.push({
        mesh: mesh,
        body: body
    })
    updateMeshes.push(mesh)

}





