/**
 * Animate
 */
const clock = new THREE.Clock()
let oldElapsedTime = 0;
let currentIntersect;

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - oldElapsedTime;
    oldElapsedTime = elapsedTime;


    //Physics world
    world.step(1/60, deltaTime, 3)

    //Updates threejs (mesh)
    objectsToUpdate.forEach(element => {
        element.mesh.position.copy(element.body.position)
        element.mesh.quaternion.copy(element.body.quaternion)
        element.body.applyForce(new CANNON.Vec3(-1,0,0), element.body.position)

    })

    // Update controls
    // controls.update()
    raycaster.setFromCamera(mouse, camera)
    const intersects = raycaster.intersectObjects(updateMeshes)
    currentIntersect = intersects

/*     if(intersects.length){
            console.log('mouse entered');
    }else{
            console.log('mouse leave')
    
    } */

    // console.log(intersects);
   
    
    // sphere.position.copy(sphereBody.position)
    /* sphere.position.x = sphereBody.position.x
    sphere.position.y = sphereBody.position.y
    sphere.position.z = sphereBody.position.z */

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()